import React, {Fragment, useEffect} from 'react';
import './modelPopup.css'
function ViewProjectModel(props) {
  // window.scroll(0,0);
  useEffect(()=>{
    if(props.show){
      document.getElementsByTagName('html')[0].classList.add("body_overflow")
    }else document.getElementsByTagName('html')[0].classList.remove("body_overflow")
  },[props.show])
  return (
    props.show ?
    <Fragment>
      <div className={(props.show?'show':'')+" model view-model"}>
        <div className="model-head d-flex justify-content-between">
          <h2>{props.data.title}</h2>
          <div>
            <i className="material-icons cursor" onClick={props.CloseModel}>close</i>
          </div>
        </div>
        <hr className="my-2"/>
        <div className="model-content">
          <div className="row">
            <div className="col-sm-6">
              <img src={props.data.pro_image} className="img-fluid border border-info p-2" alt="proImage" />
              <p/>
            </div>
            <div className="col-sm-6">
              <h3>{props.data.title}</h3>
              <p className="lead">{props.data.Discription}</p>
              {props.data.url && <a href={props.data.url} target="_blank" rel="noopener noreferrer">Live link</a>}
              <hr className="my-2"/>
              <blockquote className="blockquote">
                <p className="mb-1">Technologies Used:</p>
                <footer className="blockquote-footer position-relative">
                  {props.data.technologies.map(item=> <kbd className="badge-primary badge-pill mx-1" key={item}>{item}</kbd>)}
                  </footer>
              </blockquote>
              <hr className="my-2"/>
              <blockquote className="blockquote">
                <p className="mb-1">Category:</p>
                <footer className="blockquote-footer position-relative">
                  {props.data.category.map(item=> <kbd className="badge-light badge-pill mx-1" key={item}>{item}</kbd>)}
                  </footer>
              </blockquote>
            </div>
          </div>
        </div>
        <div className="model-footer"></div>
      </div>
      <div className="modelBg" onClick={props.CloseModel}></div>
    </Fragment>:<div className={" model view-model"}></div>
  );
}

export default ViewProjectModel;
