import React, { useState } from "react";
import ViewProjectModel from "../../components/view-project-model";
import "./style.css";

function isCategory(item, view) {
  let hasItem = item.category.find(
    (item) => item.toLocaleLowerCase() === view.toLocaleLowerCase()
  );
  return hasItem ? true : false;
}

function Portfolio() {
  const [view, setView] = useState("");
  const [modalData, setModalData] = useState({});
  const [modalshow, setModalsShow] = useState(false);
  //const [state, dispatch] = useReducer(reducer, initialState);

  const CloseModel = () => {
    setModalsShow(false);
    setModalData({});
  };

  //  useEffect()

  return (
    <div className="main portfolio">
      <div className="portfolio-box px-3">
        <h1 className="title text-center px-3 my-5">Portfolio</h1>
        <ul className="d-flex p-0 category border-bottom">
          <li
            className={"list-group-item py-1" + (view === "" ? " active" : "")}
            onClick={() => setView("")}
          >
            All
          </li>
          {category.map((item, index) => (
            <li
              key={index}
              className={
                "list-group-item py-1" + (view === item ? " active" : "")
              }
              onClick={() => setView(item)}
            >
              {item}
            </li>
          ))}
        </ul>

        <div className="row">
          <div className="col">
            <ul className="portfolio-items d-flex p-0 flex-wrap">
              {data.map((item) => {
                if (view === "" || isCategory(item, view))
                  return (
                    <li className="item" key={item.id}>
                      <img
                        className="shadow-sm"
                        src={item.pro_image}
                        alt={item.title}
                      />
                      <div className="projectDetail">
                        {item.url && (
                          <a
                            className="text-white"
                            href={item.url}
                            target="_blank"
                            rel="noopener noreferrer"
                          >
                            <i className="material-icons cursor">link</i> &nbsp;
                          </a>
                        )}{" "}
                        <i
                          className="material-icons cursor"
                          onClick={() => {
                            setModalData(item);
                            setModalsShow(true);
                          }}
                        >
                          fullscreen
                        </i>
                      </div>
                    </li>
                  );
                return true;
              })}
            </ul>
          </div>
        </div>
      </div>
      <ViewProjectModel
        data={modalData}
        CloseModel={CloseModel}
        show={modalshow}
      />
    </div>
  );
}

export default Portfolio;

